import { Link } from 'react-router-dom';
import React from 'react';

import '../App.css';

const Nav = () => {
  return <div>
    <div className="navbar">
      <Link className="navbarMenu" to={'/userList'}><b> </b></Link>
      <Link className="navbarMenu" to={'/GraphPage'}><b> </b></Link>
      <Link className="navbarMenu" to={'/aiModel'}><b>AI Model</b></Link>
      <Link className='navbarMenu' to={'/'}><b> </b></Link>
    </div>
  </div>
};


export default Nav;